.accordions {
  padding-top: 120px;
  padding-bottom: 120px;
  background: #f8f9fc;
}

.accordions h2 {
  margin: 0;
  margin-bottom: 40px;
  color: #102654;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;

  @include vp-1199 {
    text-align: center;
    font-size: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.accordions__wrapper {
  display: grid;
  grid-template-columns: 260px 1fr;
  gap: 40px;

  @include vp-1199 {
    grid-template-columns: 1fr;
  }
}

.accordions__controls {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 10px;

  @include vp-1199 {
    flex-direction: row;
    justify-content: center;
  }

  @include vp-767 {
    gap: 8px;
  }
}

.accordions__controls button {
  padding: 18px 0;
  background-color: transparent;
  border: 1px solid rgb(16, 38, 84, 0.2);
  color: #102654;
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  cursor: pointer;

  @include vp-1199 {
    flex-basis: 162px;
  }

  @include vp-767 {
    flex-basis: 141px;
    padding: 14px 0;
  }
}

.accordions__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
